::-webkit-scrollbar {
    display: none;
}

nav {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
}

.nav-bar {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

.nav-link {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: auto;
    user-select: auto;
    -webkit-user-drag: auto;
    -webkit-touch-callout: auto;
    opacity: .4;
}

.nav-link-text {
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: darkred;
    text-decoration-style:double;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    -moz-animation: counterflash 1s linear infinite;
    -webkit-animation: counterflash 1s linear infinite;
    animation: counterflash 1s infinite;
}

.nav-link-image {
    background-image: url("../public/images/link/Button.png");
    background-color: transparent;
    background-size:cover;
    background-position: center;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
}

.nav-link:hover {
    transform: scale(1.1);
    transition: transform 0.5s;
    filter: brightness(1.2);
}

.app {
    text-align: center;
    background-color: black;
}

.loading-screen {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100vw;
    height: 100vh;
    pointer-events: none;
    user-select: none;
    -webkit-user-drag: none;
    -webkit-touch-callout: none;
    z-index: 10000;
}

.loading-screen-container {
    background-color: black;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100vw;
    height: 100vh;
    z-index: 10000;
}

.loading-screen-container-image {
    background-image: radial-gradient(circle closest-side, rgba(255, 0, 0, 0.75), rgba(0, 0, 255, 0.25)), url("../public/images/background/Hexagon.jpg");
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    -moz-animation: fadein 5s linear;
    -webkit-animation: fadein 5s linear;
    animation: fadein 5s linear;
    z-index: 10001;
}

.loading-screen-icon-container {
    border-radius: 50%;
    background-color:transparent;
    position: absolute;
    top: 38vh;
    width: 46vh;
    height: 46vh;z-index: 10002;
}

.loading-screen-icon-image {
    border-radius: 50%;
    background-image: radial-gradient(circle closest-side, rgba(255, 0, 0, 0.75), rgba(0, 0, 255, 0.25));
    background-size: cover;
    background-position: center;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    z-index: 10003;
}

.loading-screen-icon-sub-image {
    border-radius: 50%;
    background-image: radial-gradient(circle closest-side, rgba(255, 0, 0, 0.75), rgba(0, 0, 255, 0.25)), url("../public/images/background/Loading.png");
    background-size: cover;
    background-position: center;
    transform-style: preserve-3d;
    transition: transform 0.5s;
    position: relative;
    top: 10%;
    left: 10%;
    width: 80%;
    height: 80%;
    z-index: 10003;
}

/* The animation code */
@keyframes rotateXRight {
    0% {
        transform: rotateX(0deg);
    }
    100% {
        transform: rotateX(360deg);
    }
}

/* The animation code */
@keyframes rotateYRight {
    0% {
        transform: rotateY(0deg);
    }
    100% {
        transform: rotateY(360deg);
    }
}

/* The animation code */
@keyframes rotateZRight {
    0% {
        transform: rotateZ(0deg);
    }
    100% {
        transform: rotateZ(360deg);
    }
}

/* The animation code */
@keyframes rotateAllRight {
    0% {
        transform: rotateZ(0deg);
    }
    100% {
        transform: rotateZ(360deg);
    }
}

/* The element to apply the animation to */
.underlay {
    pointer-events: none;
    user-select: none;
    -webkit-user-drag: none;
    -webkit-touch-callout: none;
    position: relative;
    top: 0px;
    left: 0px;
    height: 100vh;
    width: 100vw;
    z-index: 1;
    opacity: .6;
}

.underlay-background-bad {
    background-image: radial-gradient(circle closest-side, rgba(255, 0, 0, 0.75), rgba(0, 0, 255, 0.25)), url("../public/images/background/Hexagon.jpg");
    background-size: cover;
    background-position: center;
    position: absolute;
    visibility: visible;
    overflow: hidden;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    -moz-animation: pulse 2s linear infinite;
    -webkit-animation: pulse 2s linear infinite;
    animation: pulse 2s linear infinite;
    z-index: -1;
}

.underlay-background-good {
    background-image: radial-gradient(circle closest-side, rgba(235, 235, 235, 0.75), rgba(255, 255, 0, 0.25)), url("../public/images/background/Hexagon.jpg");
    background-size: cover;
    background-position: center;
    position: absolute;
    visibility: visible;
    overflow: hidden;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    -moz-animation: pulse 2s linear infinite;
    -webkit-animation: pulse 2s linear infinite;
    animation: pulse 2s linear infinite;
    z-index: -1;
}

/* The animation code */
@keyframes pulse {
    from {
        /*clip-path: circle(50% at 50% 50%); /* start from a centered circle */
        filter: hue-rotate(0deg);
    }
    to {
        /*clip-path: circle(50% at 50% 50%); /* start from a centered circle */
        filter: hue-rotate(360deg);
    }
}

@keyframes counterflash {
    from {
        filter: hue-rotate(0deg);
    }
    to {
        filter: hue-rotate(-360deg);
    }
}

