}

.game-link-container {
    position: absolute;
    width: 2048px;
    height: 2048px;
    background-color: transparent;
    opacity: 1;
    z-index: 15;
}

.game-link-sub-container {
    position: absolute;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    background-color: transparent;
    background-size: stretch;
    background-position: center;
    -moz-animation: gamelinkflash 1s infinite;
    -webkit-animation: gamelinkflash 1s infinite;
    animation: gamelinkflash 1s infinite;
    opacity: .4;
    z-index: 20;
    pointer-events: auto;
    -webkit-touch-callout: auto;
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    clip-path: circle(50% at 50% 50%);

}

@keyframes gamelinkflash {
    from {
        filter: hue-rotate(0deg);
    }
    to {
        filter: hue-rotate(-360deg);
    }
}

.game-link-sub-container-portal {
    position: absolute;
    background-size: cover;
    background-position: center;
    opacity: .1;
    z-index: 19;
    -webkit-user-select: none; /* Safari */
